.product { 
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
} 



.product:hover { 
    transform: rotate(360deg);
    transition: 2s ease-in-out;
    border: 1px solid gray;
    box-shadow: 2px 2px lightgrey;
} 

.product_info { 
    height: 100px;
    margin-bottom: 15px;
    
} 

.product_price{ 
    margin-top: 5px;
    
} 

.product_rating { 
    display: flex;
    
} 

.product > img { 
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
} 

/*.product > img:hover { 
    transform: scale(1, 2);
    transition: 3s ease-in-out;
}*/

.product > button { 
    background-color: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    
} 

.product > button:hover { 
    background-color: #35ec7b;
    color: white;
    
}