.login { 
    height: 100vh;
   display: flex;
   flex-direction: column; 
   align-items: center;
   background-color: white;
} 

.login_logo { 
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 200px;
    margin-left: auto;
    margin-right: auto;
} 

.login_container { 
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid lightgray;
    border-radius: 5px;
}

.login_container > h1 { 
    font-weight: 500px;
    margin-bottom: 20px;
}

.login_container > form > h5 { 
    margin-bottom: 5px;
}

.login_container > form > input { 
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}

.login_container > p { 
    margin-top: 15px;
    font-size: 12px;
}

.login_signinBtn { 
    background-color: #f0c14b;
    border: 1px solid;
    width: 100%;
    height: 30px;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
} 

.login_registerBtn { 
    border: 1px solid;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    margin-top: 10px;
    border-color: darkgray;

} 